@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  background: #4d4d4d;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-header {
  width: 100%;
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background: #ff0055;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.26);
  padding: 0 1rem;
  z-index: 5;
}

main {
  margin-top: 5rem;
}

@media (min-width: 768px) {
  .main-header {
    justify-content: space-between;
  }
}
.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #f8df00;
  border-color: #292929;
  color: #292929;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: white;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: transparent;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }
}

.side-drawer {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.main-navigation__menu-btn {
  width: 3rem;
  height: 3rem;
  background: transparent;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-right: 2rem;
  cursor: pointer;
}

.main-navigation__menu-btn span {
  display: block;
  width: 3rem;
  height: 2.5px;
  background: white;
}

.main-navigation__title {
  color: white;
}

.main-navigation__title a {
  text-decoration: none;
  color: white;
}

.main-navigation__header-nav {
  display: none;
}

.main-navigation__drawer-nav {
  height: 100%;
}

@media (min-width: 768px) {
  .main-navigation__menu-btn {
    display: none;
  }

  .main-navigation__header-nav {
    display: block;
  }
}
.form-control {
  margin: 1rem 0;
}

.form-control label,
.form-control input,
.form-control textarea {
  display: block;
}

.form-control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.form-control input,
.form-control textarea {
  width: 100%;
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
}

.form-control input:focus,
.form-control textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #510077;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border-color: red;
  background: #ffd1d1;
}
.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ff0055;
  border-radius: 4px;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
  margin-top: 20px;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  background: transparent;
  color: #ff0055;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.modal {
  z-index: 100;
  position: fixed;
  top: 20vh;
  left: 1%;
  width: 98%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #2a006e;
  color: white;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding-left: 0.2rem;
  padding-bottom: 0.2rem;
  padding-right: 0.2rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.map {
  width: 100%;
  height: 100%;
}

.leaflet-popup.popup-style {
  opacity: 1 !important;
  transform: none !important;
  bottom: 0 !important;
  left: 0 !important;
}

.leaflet-popup-content {
  width: 400px !important;
  text-align: center !important;
  font-weight: bold !important;
  transition: transform 0.3s ease !important;
}

.container {
  display: flex;
  justify-content: center;
  margin: 0 auto;
  border-radius: 10px;
  text-align: center;
}

.generatedMessage {
  font-size: 20px;
  text-align: center;
  width: 40%;
  color: white;
  margin-bottom: 80px;
}

@media (max-width: 767px) {
  .generatedMessage {
    width: 100%;
    font-size: 16px;
    margin: 10px;
  }
}

.generatedMessage:hover {
  transform: scale(1.02);
  transition: transform 0.2s ease-in-out;
}

p {
  font-weight: bold;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.process {
  font-weight: bold;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  margin-right: 10px;
}

@media (max-width: 767px) {
  .process {
    font-size: 12px;
    padding: 35px;
    text-align: center;
    width: 70%;
    box-sizing: border-box;
  }
}

.place-form {
  position: relative;
  list-style: none;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 30px;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  background: white;
}

.chat-color {
  color: snow;
}

.h1-center {
  text-align: center;
}
.welcome {
  font-size: 39px;
  text-align: center;
  color: #ff0055;
  font-weight: bold;
}

.chat-form {
  position: relative;
  list-style: none;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 30px;
  padding: 1rem;
  width: 90%;
  max-width: 40rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  background: white;
}

.chat-response {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  background-color: white;
}

.chat-message {
  padding: 7px;
  margin-bottom: 10px;
  max-width: 100%;
  border-radius: 8px;
  padding-top: 0;
  padding-bottom: 0;
}

.assistant-message {
  align-self: flex-start;
  background-color: #ff0055;
  color: white;
}

.user-guide-container {
  max-width: 600px;
  margin: auto;
  padding: 3px;
  border-radius: 8px;
  text-align: left;
  color: floralwhite;
}

.user-guide-container p {
  font-size: 16px;
  margin-bottom: 10px;
  text-align: center;
}

.user-guide-container strong {
  font-weight: bold;
}

.user-guide-container p:first-child {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}
@media (max-width: 600px) {
  .user-guide-container p {
    text-align: center;
  }
}

@media (max-width: 600px) {
  .chat-container {
    display: flex;
    flex-direction: column;
  }
  .chat-form {
    order: 2;
  }

  .user-guide-container {
    order: 1;
  }
}
.globe-emoji {
  padding: 5px;
}
.newPlace-description {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 16px;
  color: white;
  line-height: 1.5;
  text-align: center;
}

body {
  margin: 0px;
  padding: 0px;
}

* {
  box-sizing: border-box;
}
ul {
  list-style: none;
}
hr {
  border: none;
  background-color: #552626;
  margin: 2px 0;
}

a {
  text-decoration: none;
}
button {
  outline: none;
  border: none;
}
input {
  outline: none;
  border: none;
}
.logo img {
  width: 90px;
}
.img-style {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.image-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.container-style {
  position: relative;
  z-index: 0;
  width: 100vw;
  height: 80vh;
  overflow: hidden;
}

#main::before {
  position: absolute;
  content: "";
  background: radial-gradient(
    ellipse at center,
    rgba(181, 8, 8, 0.38) 0%,
    rgba(0, 0, 0, 0.6) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#61b50808', endColorstr='#99000000', GradientType=1 );
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  z-index: 0;
}

.active {
  background-color: #f3f3f3;
  color: #ffffff;
  box-shadow: 5px 10px 30px rgba(88, 198, 64, 0.411);
  transition: all ease 0.2s;
}
.active:hover {
  background-color: #000000;
  color: #ffffff;
  box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
  transition: all ease 0.2s;
}

.header-heading {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0;
}

.header-heading h2 {
  font-family: "Lato";
  font-size: 3rem;
  color: #f3f3f3;
}
em {
  color: #ff0055;
  font-size: 3.5rem;
  font-weight: bold;
}
.header-heading h1 {
  font-family: "Lato";
  font-size: 3.5rem;
  color: #f3f3f3;
}

.header-heading .details {
  font-size: 20px;
  color: #c5c5c5;
}

@media (max-width: 600px) {
  .image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
  }

  .img-style {
    width: 180%;
    height: 150%;
    object-fit: cover;
  }

  .header-heading h1,
  .header-heading h2 {
    margin: 0;
    padding: 0;
  }

  .header-heading h1 {
    font-size: 2.9rem;
  }

  .header-heading h2 {
    font-size: 1.5rem;
  }

  .header-heading .details {
    font-size: 12px;
    color: #c5c5c5;
    font-weight: 700;
  }
  .container-style {
    position: relative;
    z-index: 0;
    width: 100vw;
    height: 60vh;
    overflow: hidden;
  }
}

#features {
  width: 100%;
  height: 80vh;
  box-sizing: border-box;
  font-family: "Lato";
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  background-color: #000000;
}
#features h1 {
  color: #fff;
  font-size: 2.5rem;
  z-index: 1;
}

.a-container {
  display: flex;
  justify-content: space-between;
  align-content: space-around;
  grid-gap: 70px;
  gap: 70px;
}

.a-box {
  background-color: #000000;
  width: 240px;
  height: 340px;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
  transition: 0.4s ease-in-out;
  position: relative;
}
.a-b-img {
  width: 100%;
  height: 60%;
}
.a-b-img img {
  max-width: 100%;
  height: auto;
  padding: 15px;
  margin-top: 40px;
  width: 100%;
  height: 60%;
}
.a-box:hover {
  border: 1px solid #ff1414;
  box-shadow: 2px 2px 12px rgba(184, 0, 0, 0.445);
}
.a-b-text {
  width: 100%;
  height: 40%;
  background-color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.a-b-text h2 {
  color: #ff0055;
}
.a-b-text p {
  margin: 0px;
  color: #ffffffa6;
  font-size: 0.9rem;
  font-family: "Lato";
  display: block;
  display: -webkit-box;
  max-width: 80%;
  margin-top: 5px;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 10px;
  padding-bottom: 15px;
}
.a-b-text p:hover {
  color: #ffffff;
}
.a-b-text a {
  margin-top: 15px;
}
#presentaion {
  padding-top: 30px;
  width: 100%;
  height: 768px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

.pr-heading {
  text-align: center;
  font-family: "Lato";
  width: 600px;
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -55%);
}
.pr-heading span {
  color: #ff1414;
}
.pr-heading .details {
  font-size: 23px;
  color: #c5c5c5;
}
.pr-heading .details {
  font-size: 1.2rem;
}
.pr-heading h1 {
  font-family: "Lato";
  font-size: 4rem;
  margin: 0px;
  letter-spacing: 2px;
  color: #ffffff;
}
.pr-btns {
  display: flex;
  margin-top: 40px;
  margin-left: 35%;
}
.pr-btn {
  width: 160px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  background-color: #ff1414;
  box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
  border-radius: 5px;
  color: #ffffff;
}
.pr-btn:hover {
  background-color: transparent;
  transition: all ease 0.5s;
  color: #ffffff;
  border: 2px solid #ff1414;
}

#about {
  width: 100%;
  height: auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px 5%;
  position: relative;
  background-color: #282424;
}

.about-text {
  width: 80%;
  color: #fff;
  text-align: center;
}

.about-text h1 {
  font-size: 2.5rem;
  color: #ff0055;
  font-weight: bold;
  margin: 20px 0 10px;
}

.about-text p {
  width: 79%;
  font-size: 1rem;
  color: #cecece;
  margin-bottom: 20px;
  margin: 0 auto;
}

.about-image {
  width: 70%;
  margin-top: 20px;
}

.about-image img {
  max-width: 100%;
  height: auto;
  border-radius: 30px;
}

@media (max-width: 767px) {
  #about {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .about-text,
  .about-image {
    width: 100%;
  }
  .about-image {
    margin-right: 80px;
  }

  #about .about-text h1 {
    font-size: 20px;
  }
  .a-container {
    display: flex;
    justify-content: space-between;
    align-content: space-around;
    grid-gap: 0;
    gap: 0;
  }

  .header-heading {
    margin-bottom: 20px;
  }

  #features {
    margin-top: 20px;
  }
}

@media (min-width: 768px) {
  #about {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
  }
}

#contact {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#contact h1 {
  color: #fff;
  font-size: 3rem;
}
#contact form {
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}
#contact form input,
#contact form textarea {
  width: 100%;
  height: 50px;
  margin: 5px 0px;
  padding: 10px;
  border: none;
  outline: none;
  background-color: #ffffff2d;
  color: #ffffff;
  border-radius: 5px;
}
#contact form textarea {
  height: 150px;
}
#contact form input[type="submit"] {
  height: 45px;
  background: linear-gradient(90deg, #ff1414 20%, #f70000);
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 620px) {
  #contact form {
    width: 90%;
  }
}

@media (max-width: 1190px) {
  #main {
    background-size: 1150px !important;
  }
  .pr-heading {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
  }
  .pr-heading h2 {
    font-size: 1.5rem;
  }
  .pr-heading h1 {
    font-size: 3rem;
  }
  .pr-heading .details {
    font-size: 1rem;
  }
  .about-image img {
    height: 400px;
  }
  #features {
    height: auto;
  }
  .a-container {
    flex-wrap: wrap;
  }
  .a-box {
    flex-grow: 1;
  }
  .a-b-img img {
    object-fit: contain;
  }
}

@media (max-width: 970px) {
  #main {
    height: 650px;
  }
  .pr-heading {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .header-btn {
    margin-left: 20%;
    margin-top: 20px;
  }
  .pr-btn {
    margin-left: 20%;
    margin-top: 20px;
  }
  #about {
    justify-content: center;
    padding-top: 0px;
    text-align: center;

    margin-top: 0px;
  }
  .about-text {
    width: 90%;
  }
  .about-text h1 {
    font-size: 4rem;
  }
  .about-text p {
    width: 100%;
  }
  #features {
    margin-bottom: -4px;
  }
}

